import React, { Component } from 'react';
import axios from 'axios';
import { Link } from 'react-router-dom';

class OrderList extends Component {
  constructor(props) {
    super(props);
    this.state = {
      orders: [],
      error: null,
    };
  }

  componentDidMount() {
    this.fetchOrders();
  }

  fetchOrders() {
    axios.get('https://server-6kol.onrender.com/api/v1/orders/')
      .then(response => {
        this.setState({ orders: response.data });
      })
      .catch(error => {
        this.setState({ error });
      });
  }

  handleDownloadExcel = () => {
    axios({
      url: 'https://server-6kol.onrender.com/api/v1/orders/downloads/',
      method: 'GET',
      responseType: 'blob', // :  blob   
    }).then((response) => {
      const url = window.URL.createObjectURL(new Blob([response.data]));
      const link = document.createElement('a');
      link.href = url;
      link.setAttribute('download', 'data.xlsx'); //    
      document.body.appendChild(link);
      link.click();
    }).catch(error => {
      console.error('   :', error);
    });
  }

  render() {
    const { orders, error } = this.state;

    if (error) {
      return <div> : {error.message}</div>;
    }

    return (
        <div>
          <h2>
          <Link to={'/orders/new'}>  </Link></h2>
          <h2> </h2>
          <button onClick={this.handleDownloadExcel}> </button>
          <ul style={{ listStyleType: 'none', padding: 0 }}>
            {orders.map(order => (
              <li key={order.orderNumber} style={{ display: 'flex', marginBottom: '10px' }}>
                <div style={{ marginRight: '20px' }}>
                  <Link to={`/orders/${order.id}`}> : {order.orderNumber}</Link>
                </div>
                <div style={{ marginRight: '20px' }}> : {order.ordererName}</div>
                <div style={{ marginRight: '20px' }}>: {order.affiliation}</div>
                <div style={{ marginRight: '20px' }}>: {order.contact}</div>
                <div style={{ marginRight: '20px' }}> : {order.orderStatus}</div>
                <div style={{ marginRight: '20px' }}>: {order.creator}</div>
                <div style={{ marginRight: '20px' }}> : {order.creationTime}</div>
                <div style={{ marginRight: '20px' }}>: {order.totalAmount}</div>
                <div>: {order.balance}</div>
              </li>
            ))}
          </ul>
        </div>
      );
      
      
  }
}

export default OrderList;
